body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Form-div {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: space-between;
  color: black;
}

.Form-leftDiv .button {
  font-size: 0.7em;
}

.Form-leftDiv {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  padding: 20px;
  font-size: 2em;
}
.Form-leftDiv input[type=file] {
  display: none;
}

.Form-leftDiv .buttonDiv {
  display: flex;
  justify-content: space-between;
  flex-shrink: 0;
}
.Form-leftDiv .label {
  cursor: pointer;
  font-size: 1em;
  background: rgb(255, 215, 155);
  color: rgb(235, 77, 3);
  padding: 5px 20px;
  flex: 1 1;
  transition: all .4s;
  border: 0;
  outline: 0;
  text-decoration: none;
  flex-shrink: 0;
}
.Form-leftDiv .label:hover {
  background: rgb(202, 54, 9);
  color: rgb(255, 255, 250);
  transition: all .4s;
}
.Form-leftDiv .label:disabled {
  cursor: default;
  background: rgb(123, 139, 155);
  color: rgb(203, 209, 208);
  transition: all .4s;
}
.Form-leftDiv .arrowDown {
  margin: 0;
  padding: 0;
  font-size: 1em;
  color: rgb(255, 223, 205);
  font-weight: bolder;
  -webkit-transform: scaleX(0.7);
          transform: scaleX(0.7);
  -webkit-transform-origin: center center;
          transform-origin: center center;
}

.Form-rightDiv {
  flex: 2 1;
  display: flex;
  flex-direction: column;
  padding: 20px 20px 20px 20px;
  font-size: 1.5em;
}
.Form-rightDiv h1 {
  text-align: left;
  padding: 0; margin: 0;
  color: rgb(155,155,155);
  font-weight: 100;
}
.Form-textResult {
  flex: 1 1;
  resize: none;
}
.Synthesiser {
  display: inline-block;
  vertical-align: middle;
  min-width: 17em;
}

.Synthesiser * {
  vertical-align: middle;
}

.Synthesiser .box {
  display: inline-block;
  position: relative;
  vertical-align: top;
}

.Synthesiser .margin-right-10 {
  margin-right: 10px;
}

.Synthesiser .small-em {
  height: 0.7em;
}

.Synthesiser .orange-text {
  color: #eb4d03;
}
.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-wobble infinite alternate-reverse 1s ease-in-out;
  height: 12vmin;
  padding-right: 10px;
}

.App-bg {
  display: block;
  width: 100vw;
  position: absolute;
  z-index: 1;
  -webkit-transform: scaleX(1.01) scaleY(1.01);
          transform: scaleX(1.01) scaleY(1.01);
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.App-header {
  padding: 20px 20px 0 20px;
  height: 20vh;
  width: 100vw;
  display: flex;
  position: relative;
  overflow: hidden;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 4vmin);
  color: rgb(255, 255, 255);
  box-sizing: border-box;
}
.App-form {
  min-height: 80vh;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-wobble {
  from { -webkit-transform: translate(0, 0px) scaleY(1); transform: translate(0, 0px) scaleY(1); }
  to { -webkit-transform: translate(0, -5px) scaleY(1.05); transform: translate(0, -5px) scaleY(1.05);}
}

@keyframes App-logo-wobble {
  from { -webkit-transform: translate(0, 0px) scaleY(1); transform: translate(0, 0px) scaleY(1); }
  to { -webkit-transform: translate(0, -5px) scaleY(1.05); transform: translate(0, -5px) scaleY(1.05);}
}

